

/**
 * 
 * @param {验证用户名} value 
 */
export function VerificationUserName(value){
    var uPattern = /^[\u4E00-\u9FFF]{2,16}$/;
    var msg= { valid: uPattern.test(value), msg: '用户名输入错误'}
    return msg;
}


export function VerificationCarNumber(value){
    var uPattern =/^[京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领A-Z]{1}[A-Z]{1}[A-Z0-9]{4}[A-Z0-9挂学警港澳]{1}$/;
    var msg= { valid: uPattern.test(value), msg: '车牌号输入错误'}
    return msg;
}


