
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { IrouteList } from "@/api/iroute";
import {
VerificationUserName,
VerificationCarNumber
} from "@/api/Verification";
import { Config } from "@/uilt/uilt";
import { createDriverinfo } from "@/api/driver";
import service from "@/uilt/request";

export default {
props: {},
data() {
var dataset = {
dataSet: {
username: "",
mobile: "",
usercall: "",
carcard: "",
carColor: "",
carType: "舒适5座",
seatNum: "4", //座位数
route: [],
driverlicense: "", //驾驶证
idcard: "", //身份证
drivinglicense: "", //行驶证
carimage: "", //车辆侧面照
operationcertificate: "" 
},
usernameType: VerificationUserName,
carNumberType: VerificationCarNumber,
carTypeEnum: ["舒适5座", "商务7座"],
seatNumEnum: [4, 5, 6, 7, 8, 9, 10],
IrouteOptions: [],
show: 0
};
Config("operationcertificate").then(function(result) {
dataset.show = result;
});
return dataset;
},
computed: {},
created() {
var list = [
"onMenuShareTimeline",
"onMenuShareAppMessage",
"onMenuShareQQ",
"onMenuShareWeibo",
"onMenuShareQZone",
"chooseImage",
"getNetworkType",
"hideOptionMenu",
"showOptionMenu",
"hideMenuItems",
"uploadImage",
"showMenuItems",
"hideAllNonBaseMenuItem",
"showAllNonBaseMenuItem",
"closeWindow"
];
var dataset = this;
service
.get(process.env.BASE_API + "apiticket?url=" + window.location.href)
.then(signature => {
dataset.$wechat.config({
debug: true,
appId: "wx6a2e25ee7abfca60",
timestamp: "1414587457",
nonceStr: "Wm3WZYTPz0wzccnW",
signature: signature,
jsApiList: list
});
});


IrouteList().then(function(result) {
result.forEach(element => {
dataset.IrouteOptions.push({
name: element.routeName,
value: element.id,
parent: 0
});
});
});
},
mounted() {},
watch: {},
methods: {
selectImage() {
wx.chooseImage({
count: 1, // 默认9
sizeType: ["original", "compressed"], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ["album", "camera"], // 可以指定来源是相册还是相机，默认二者都有
success: function(res) {
localIds = res.localIds;
}
});
},
showImage() {
wx.previewImage({
current: "", // 当前显示图片的http链接
urls: [] 
});
},
localhostImage(id, ele) {
this.$wechat.getLocalImgData({
localId: id,
success: function(res) {
var localData = res.localData;
var img = document.createElement("img");
if(localData[0]=='d'&&localData[1]=='a'&&localData[2]=='t'&&localData[3]=='a'&&localData[4]==':'){
localData= localData.replace("data:img","data:image");
}else{
localData+='data:image/jpeg;base64,'+localData;
}
img.src = localData;
img.width = 160;
ele.appendChild(img);
}
});
},
uploadImage(id, ele) {
var dataset = this.dataSet;
this.$wechat.uploadImage({
localId: id,
isShowProgressTips: 1,
success: function(res) {
var serverId = res.serverId;
dataset[ele] = serverId;
}
});
},
uploadWxImage(datatype) {
var dataset = this;
this.$wechat.chooseImage({
count: 1, //默认9
sizeType: ["original", "compressed"], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ["album", "camera"],
success: function(res) {
console.log(res);
try {
var tmp = res.localIds;

var image = document.getElementById(datatype);
dataset.localhostImage(tmp[0], image);
dataset.uploadImage(tmp[0], datatype);
} catch (e) {
alert(e);
}
}
});
},
commit: function() {
console.log(this.dataSet);
alert("正在提交,请等待");
if (
this.dataSet.carimage === "" ||
this.dataSet.driverlicense === "" ||
this.dataSet.drivinglicense === "" ||
this.dataSet.idcard === ""
) {
this.$vux.toast.show({ text: "证件信息不完整" });
return;
}

if (
this.dataSet.username == "" ||
this.dataSet.carColor == "" ||
this.dataSet.carcard == "" ||
this.dataSet.usercall == "" ||
this.dataSet.mobile == "" ||
this.dataSet.route == undefined
) {
this.$vux.toast.show({ text: "用户信息不完整" });
return;
}
this.dataSet.route=this.dataSet.route[0];
var that = this;
createDriverinfo(this.dataSet).then(function(result) {
alert("创建成功等待审核,这需要几个工作日");
that.$wechat.closeWindow();
});
}
},
components: {
Cell,
CellBox,
CellFormPreview,
Group,
Badge,
XInput,
PopupPicker,
PopupRadio,
XButton
}
};



